import React from 'react';
import './sass/App.sass';


//component
import Header from './сomponent/elements/Header'
import NavBar from './сomponent/elements/NavBar'
import SocialIcon from './сomponent/elements/SocialIcon'
import Home from './сomponent/pages/Home'
import BlockCounter from "./сomponent/elements/BlockCounter";

//files

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            openMenu: false,
            currentBlock: 0,
            top: document.documentElement.clientHeight / 2,
        };
    }

    toggleMenu(e) {
        e.preventDefault();
        this.setState({openMenu: !this.state.openMenu});
    }

    pageScrollListener(e) {
        const scroll = e.target.scrollTop,
            height = document.documentElement.clientHeight;
        this.setState({
            currentBlock: Math.round(scroll / height),
            top: scroll + height / 2,
        });
    }

    closeMenu(e) {
        e.preventDefault();
        this.setState({openMenu: !this.state.openMenu});
    }

    render() {
        return (
            <div>
                <Header active={this.state.openMenu} toggleMenu={this.toggleMenu.bind(this)}/>
                <div className={this.state.openMenu ? 'app menu' : 'app'}>
                    <Home
                        onClick={this.closeMenu.bind(this)}
                        scrollHandler={this.pageScrollListener.bind(this)}
                        active={this.state.openMenu}
                    >
                        <BlockCounter currentBlock={this.state.currentBlock} />
                    </Home>
                </div>
                <NavBar/>
                <SocialIcon className={'bottom'} active={this.state.openMenu} />
                {/*<SocialIcon active={this.state.openMenu} className={'right'} />}*/}

            </div>
        );
    }
}

export default App;
