import React from 'react';

class BlockCounter extends React.Component {
    constructor(props) {
        super(props);
        this.initPoint = [{
            text: 'HOME',
            index: 0
        }, {
            text: 'ABOUT US',
            index: 1
        }, {
            text: 'SERVICES',
            index: 2
        }, {
            text: 'PORTFOLIO',
            index: 3
        }//,{
            //     text: 'BLOG',
            //     index: 4
            // }
        ]
    }

    render() {
        return (

            <div className={'block-counter'}>
                {/*<li key={i} className={this.props.currentBlock === i && 'active'}>*/}
                <ul>
                    {this.initPoint.map((set, i) => (
                        <li key={i} className={this.props.currentBlock === i ? 'active' : ''}>
                            <div className="number">0{i+1}</div>
                            <div className="block-counter__wrap">
                                <div className="point" />
                                <span>{set.text}</span>
                            </div>

                            <div className="line" />
                        </li>
                    ))}
                </ul>
            </div>
        );
    }
}

export default BlockCounter;
