import React from 'react';

import '../../sass/navbar.sass';


function NavBar(props) {
    let menuItem = [//{
    //     text: 'HOME',
    //     to: '/'
    // },
    {
        text: 'ABOUT US',
        to: '//'
    },{
        text: 'SERVICES',
        to: '//'
    },{
        text: 'PORTFOLIO',
        to: '//'
    },{
        text: 'BLOG',
        to: '///'
    }];
    return (
        <nav className={'menu'}>
            <ul>
                <li><a className={'active'} onClick={e => e.preventDefault()} href="/">HOME</a></li>
                {
                    menuItem.map((set, i) => (
                        <li key={i}>
                            <a href={set.to} onClick={e => e.preventDefault()}>
                                {set.text}
                            </a>
                        </li>
                    ))
                }
            </ul>
        </nav>
    );

}

export default NavBar;
