import React, {Component} from 'react';

import '../../sass/home.sass';
import SocialIcon from "../elements/SocialIcon";

class Home extends Component {

    render() {
        return (
            <div onScroll={this.props.scrollHandler} className="page">
                <section className={'first'}>
                    <div className="bg-filter">
                        <div className="v">
                            <div className="v-child">

                            </div>
                        </div>
                    </div>

                </section>
                <section></section>
                <section></section>
                <section></section>
                <footer></footer>
                <SocialIcon active={this.props.active} className={'right'} />
                {this.props.children}
            </div>
        );
    }
}

export default Home;
