import React from 'react';

function SocialIcon(props) {
    const socialIcon = [
        // {name: 'facebook', icon: ''},
        // {name: 'twitter', icon: ''},
        {name: 'telegram', icon: <i className="fab fa-telegram-plane" />, href: 'tg://resolve?domain=edge_space'},
        // {name: 'Behance', icon: ''},
        // {name: 'Instagram', icon: ''},
        {name: 'LinkedIn', icon: <i className="fab fa-linkedin-in" />, href: 'https://www.linkedin.com/in/yurii-papka-576157182/'},
        {name: 'Skype', icon: <i className="fab fa-viber" />, href: 'skype:papka.yurii'},
        {name: 'Viber', icon: <i className="fab fa-skype" />, href: 'viber://chat?number=+120345678910'},
    ];
    return (
        <div style={{top: props.top + 'px'}} className={'social-icon ' + props.className}>
            <ul className={props.active ? 'active' : ''}>
                {socialIcon.map((icon, i) => (
                    <li key={i}>
                        <a target={~icon.href.indexOf('www') ? "_blank" : ""} href={icon.href} title={icon.name}>
                            {icon.icon}
                        </a>
                    </li>
                ))}
            </ul>
        </div>
    );
}

export default SocialIcon;
